<template>
  <div>
    <div class="d-flex text-center text-white mb-4">
      <div
        v-for="(button, index) in group_buttons"
        :key="index"
        :data-index="index"
        :class="{
          'w-100': v_100
        }"
      >
        <span
          class=" px-3 py-2 border click-pointer d-block"
          :class="{
            'bg-secondary': current_onglet !== button.id,
            'bg-primary': current_onglet === button.id
          }"
          @click="select_curent_onglet(button, index)"
          v-html="button.value"
        ></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MenusButtons",
  props: {
    group_buttons: {
      type: Array,
      validator: function(menus) {
        if (!menus.length) {
          return true;
        } else {
          return true;
        }
      }
    },
    // pour que les bouton occupe tout l'espace disponible.
    v_100: { type: Boolean, default: false }
  },
  data() {
    return {
      current_onglet: ""
    };
  },
  watch: {
    group_buttons: {
      deep: true,
      handler: function(val) {
        if (val[0]) {
          this.select_curent_onglet(val[0], 0);
        }
      }
    }
  },
  computed: {
    //
  },
  mounted() {
    //console.log("load MenusButtons V2");
  },
  methods: {
    select_curent_onglet(button, index) {
      this.current_onglet = button.id;
      this.$emit("ev-menu-button", {
        index: index,
        ...button
      });
    }
  }
};
</script>
