<template lang="html">
  <div class="form-group" :class="[image.ShowCover ? 'bg-cover' : '']">
    <div class="row">
      <div class="col-sm-7" v-if="image.url === ''">
        <b-form-group label="Image">
          <b-form-file
            @change="previewAndSaveImage"
            accept="image/*"
            placeholder="Choose a file or drop it here..."
            drop-placeholder="Drop file here..."
          ></b-form-file>
        </b-form-group>
      </div>
      <div class="col-sm-4">
        <div v-if="image.url">
          <img
            class="img-fuild"
            alt=""
            :src="image.url"
            style="max-width: 140px; max-height: 100px;"
          />
        </div>
      </div>
      <div class="col-sm-7" v-if="image.url !== ''">
        <ButtonDelete
          :running="running"
          @ev-click="$emit('ev-delete-file', image.key)"
        ></ButtonDelete>
      </div>
    </div>
    <h4 class="text-wait">Patientez ...</h4>
  </div>
</template>

<script>
//
import { ButtonDelete } from "wbuutilities";
export default {
  name: "ImageShopify",
  props: {
    image: {
      type: Object,
      required: true,
      validator: function(val) {
        if (
          (val.url || val.url == "") &&
          (val.alt || val.alt == "") &&
          //(val.ShowCover || val.ShowCover == "") &&
          (val.key || val.key == "") &&
          (val.filename || val.filename == "")
        )
          return true;
        return false;
      },
      default: function() {
        return {
          url: "",
          alt: "",
          title: "",
          filename: "",
          key: "",
          ShowCover: false
        };
      }
    }
  },
  components: {
    ButtonDelete
  },
  data() {
    return {
      running: false
    };
  },
  mounted() {
    //
  },
  watch: {
    //
  },
  computed: {
    //
  },
  methods: {
    /**
     * Convertie l'image en base 64 et le return au parent.
     */
    previewAndSaveImage: function(event) {
      var self = this;
      if (event.target.files && event.target.files[0]) {
        self.getBase64(event.target.files[0]).then(ConvertResult => {
          self.$emit("image-save", ConvertResult);
        });
      }
    },
    getBase64: function(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        // reader.onload = () => resolve(reader.result);
        reader.onloadend = () => {
          var fileArray = reader.result.split(",");
          resolve({
            src: reader.result,
            base64: fileArray[1],
            name: file.name
          });
        };
        reader.onerror = error => reject(error);
      });
    }
  }
};
</script>

<style lang="scss" scoped></style>

